import React , {Component} from "react";
import Clock from "./Clock"
import './App.css'
import {Form, FormControl, Button, Navbar} from 'react-bootstrap';

class App extends Component {
  constructor (props) {
    super(props);
    this.state = {

      deadline: 'July 12, 2019',
      eventToChange: 'Countdown to Senior Checkpoint',
      newDeadline: '',
      anEvent: '',
      brackets: '< >'
    }
  }

  changeDeadline () {
    this.setState({eventToChange: this.state.anEvent, deadline: this.state.newDeadline})
  }

  render() {
    return (
      <div id="relative">
        <div id="top"></div>
        <Navbar collapseOnSelect expand="lg" className= "sticky">
          <Navbar.Brand href="#home" className="padding-left"><a href="#top">{this.state.brackets}</a></Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav" className="justify-content-end">
            <Navbar.Text className="padding-right">
              <a className="undo" href="#helpful">Helpful Resources</a>
            </Navbar.Text>
            <Navbar.Text className="padding-right">
              <a href="#motivation">You got this!</a>
            </Navbar.Text>
          </Navbar.Collapse>
        </Navbar>
      <div className="App">
        <div className="App-title">{this.state.eventToChange}</div>
        <div className="App-subtitle">{this.state.deadline}</div>

        <div>
          < Clock
           deadline = {this.state.deadline}/>
           <div className="App-smalltitle">Don't freak out. Change the countdown to something you're looking forward to like a beach trip!</div>
        </div>
        <div>
          <Form inline className='center'>
            <FormControl placeholder='new event'
            onChange={event => this.setState({anEvent: event.target.value})}/>
            <FormControl placeholder='new date (plz format)'
            onChange={event => this.setState({newDeadline: event.target.value})}/>
            <Button onClick={()=> this.changeDeadline()}>Submit</Button>
        </Form>
        </div>
      </div>
      <div>
        <h1 className="center-title">Helpful Resources:</h1>
        <div id="helpful"></div>
        <div className="width">
          <h3>Express</h3>
          <ul>
            <li><a target="_blank" href="https://www.youtube.com/watch?v=pKd0Rpw7O48">Youtube vid</a>: Express.js Tutorial: Build RESTful APIs with Node and Express | Mosh (Mosh is the best!)</li>
          </ul>
          <h3 className="spaceAbove">Sequelize</h3>
          <ul>
            <li><a target="_blank" href="https://www.youtube.com/watch?v=isk0JR0t_VQ">Youtube vid</a>: Express-Sequelize review part 4 - Sequelize Associations (kinda low audio quality)</li>
          </ul>
          <h3 className="spaceAbove">React</h3>
          <ul>
            <li><a target="_blank" href="https://scrimba.com/g/glearnreact">React tutorial</a> (code and watch along environment)</li>
          </ul>
        </div>
        <h1 className="center-title">You got this!</h1>
        <div id="motivation"></div>
        <img src="https://media.giphy.com/media/xT9IgEYXCNqPZnqMuY/giphy.gif" alt="You got this"></img>
      </div>
      <div className="footer">Created with help from <a target="_blank" href="https://www.skillshare.com/classes/ReactJS-and-Redux-Build-4-Web-Apps-14/1262069474">skillshare vid</a> by David Katz, Software Engineer - Coding Instructor</div>
      </div>
    )
  }
}

export default App;
